.loading-container {
  overflow: hidden;
  position: relative;
  height: 100vh;
  background-color: #1f2022;
}

.coin-image {
  position: absolute;
  top: 50%;
  left: -100px;
  width: 100px;
  height: 100px;
  animation: roll 2.5s ease-in-out infinite;
}

@keyframes roll {
  0% {
    transform: translateX(-100px) rotate(0deg);
  }
  50% {
    transform: translateX(calc(50vw - 50px)) rotate(180deg);
  }
  100% {
    transform: translateX(calc(100vw + 100px)) rotate(360deg);
  }
}
